<template>
  <div class="layout-base">
    <layout-tabs-router :title="title" :tabs="tabs" :crumbs="crumbs">
      <router-view />
    </layout-tabs-router>
  </div>
</template>

<script>
import { LayoutTabsRouter } from '@jg/via-ui';
import { reactive, toRefs, watch } from '@vue/composition-api';

export default {
  components: {
    LayoutTabsRouter,
  },
  name: 'tips-list',
  setup(_, { root }) {
    const data = reactive({
      lang: 'zh',
      crumbs: [
        {
          path: '',
          value: '官网管理',
        },
        {
          path: '',
          value: '媒体新闻',
        },
        {
          path: '',
          value: '中文',
        },
      ],
      tabs: [
        {
          path: 'zh',
          title: '中文',
        },
        {
          path: 'en',
          title: '英文',
        },
      ],
      title: '媒体新闻',
    });

    watch(
      () => root.$route.path,
      newVal => {
        data.lang = newVal.endsWith('/zh') ? 'zh' : 'en';
        data.lang === 'zh' ? (data.crumbs[2].value = '中文') : (data.crumbs[2].value = '英文');
      },
    );
    return {
      ...toRefs(data),
    };
  },
};
</script>

<style lang="less" scoped></style>
